.sptcrb__rating__block__edit__wrapper {
	background: #f3f3f4 !important;
	padding: 15px !important;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif !important;
	text-align: center !important;
}

.sptcrb__rating__block__edit__wrapper h3 {
	font-size: 13px !important;
	text-align: center !important;
	margin: 0 0 20px 0 !important;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif !important;
}

.sptcrb__rating__block__ratingbar_rating__column {
	width: 100% !important;
	display: block !important;
}

.sptcrb__rating__block__ratingbar_rating__column:first-of-type {
	text-align: right !important;
}

.sptcrb__rating__block__ratingbar_rating__column:nth-of-type(2) {
	text-align: left !important;
	padding-right: 5px !important;
}

.sptcrb__rating__block__ratingbar_rating__column input {
	text-align: left !important;
	width: 100% !important;
	margin: 0 !important;
}

.sptcrb__rating__block__ratingbar_button {
	text-align: center !important;
	margin: 20px 0 !important;
}

.sptcrb__rating__block__ratingbar_button button {
	background: #00bc87 !important;
	border: 0px solid #fff !important;
	color: #fff !important;
	padding: 10px 20px !important;
	cursor: pointer !important;
	font-size: 14px !important;
	border-radius: 3px !important;
}

.sptcrb__rating__block__ratingbar_button button:after {
	content: "+" !important;
	margin-left: 10px !important;
	font-weight: bold !important;
	padding: 0px 2px 2px 2px !important;
	display: inline-block !important;
	border-radius: 50px !important;
	width: 21px !important;
	color: #fff !important;
	border: 1px solid #fff !important;
}

.sptcrb__rating__block__checkbox {
	text-align: center !important;
	font-size: 14px !important;
	margin-top: 10px !important;
}

.sptcrb__rating__block__checkbox input {
	border-color: #00bc87 !important;
	color: #00bc87 !important;
}

.sptcrb__rating__block__checkbox input {
	margin-right: 3px !important;
}
.sptcrb__rating__block__review div {
	display: flex !important;
	margin-bottom: 10px !important;
}
.sptcrb__rating__block__review div button {
	background: #e43737 !important;
	border: #e43737 !important;
	width: 30px !important;
	color: #fff !important;
	cursor: pointer !important;
	font-weight: bold !important;
	border-radius: 3px !important;
	margin-right: 10px !important;
}
.sptcrb__rating__block__review div input[type="text"] {
	width: 55% !important;
	margin-right: 10px !important;
}
.sptcrb__rating__block__review div input[type="number"] {
	width: 25% !important;
	padding-right: 0 !important;
}

.sptcrb__rating__block__edit__wrapper input#sptcrb__rating-text {
	width: 100% !important;
	max-width: 360px !important;
	text-align: center !important;
	margin: 0 0 20px !important;
}
